.clientList {
    flex: 4;
    margin-left: 20px;
    color :white;
  }
  
  .clientListEditL:hover {
    color: #d6cb30;
  }
  
  .clientListEditL{
    color: black;
    cursor: pointer;
  }

  .clientListClient {
    display: flex;
    align-items: center;
  }
  
  .clientListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
  .clientListDelete:hover{
    color: red;
    cursor: pointer;
  }