.containerP {
  display: flex;
   flex-direction: column; 
  justify-content: center;
 align-items: center; 
   position: relative; 
  box-sizing: border-box;
  text-align: center;
  background-image: url("../../assets/images/fondo_oscuro.jpg");
}

.Administrador {
  /* background-image: url("../../assets/images/fondo_oscuro.jpg"); */
  background-color: #3c3c3c;
}

.contact-login {
  box-shadow: 0 0 20px 0 rgba(255, 255, 255, 0.3);
}

.contact-login > * {
  padding: 1em;
}

.boton-login {
  font-size: 20px;
  color: white;
  background: transparent;
  border: 2px solid #d6cb30;
  border-radius: 6px;
  padding: 6px;
}
.boton-login:hover {
  cursor: pointer;
  color: #d6cb30;
}
.boton-registro {
  font-size: 20px;
  color: white;
  background: transparent;
  border: 2px solid #d6cb30;
  border-radius: 6px;
  padding-Left: 6px;
  padding-right: 6px;
  padding-top: 7px;
  padding-bottom: 8px;
  text-decoration: none;
}
.boton-registro:hover {
  cursor: pointer;
  color: #d6cb30;
  
}

.contact-form .form-group {
  margin: 0;
  padding: 1em;
}

.contact-form form .block {
  grid-column: 1 / 3;
}

.contact-form form button,
.contact-form form input,
.contact-form form textarea,
.contact-form form option {
  width: 100%;
  padding: 0.7em;
  border: none;
  background: none;
  outline: 0;
  color: #fff;
  border-bottom: 1px solid #d6cb30;
}
.contact-form form select {
  width: 100%;
  padding: 0.7em;
  border: none;
  background: none;
  outline: 0;
  color: #fff;
  border-bottom: 1px solid #d6cb30;
  font-size: 17px;
  margin-bottom: 200px;
}
.label-error {
  font-size: 14px;
  color: red;
  background: transparent;
}

.label-alert {
  font-size: 14px;
  color: red;
  background: transparent;
}
