.padding-h1 {
  padding-bottom: 15px;
}
.padding-top {
  padding-top: 30px;
}
.divider {
  border-bottom: 2px solid #f5f5f5;
}
.carousel {
  background: #1a1a1a;
  min-height: 240px;
  padding: 40px;
  border: 2px;
  text-align: center;
  margin-bottom: 20px;
}

.carousel p {
  font-size: 17px;
  font-weight: 500;
  padding: 2px;
  color: #f5f5f5;
}

.testimonial-user {
  max-width: 240px;
  margin: 0 auto;
  text-align: left;
}

.testimonial-user h6 {
  margin-top: 20px;
  margin-left: 10px;
  color: #f5f5f5;
}

.testimonial-user img {
  border-radius: 50%;
  max-width: 70px;
  max-height: 70px;
  text-align: center;
}

.especiales {
  text-decoration: none;
  color: #1a1a1a;
  width: 3px;
}
